(function () {
    'use strict';

    angular
            .module('caloappApp')
            .controller('UserCaloriesDeleteController', UserCaloriesDeleteController);

    UserCaloriesDeleteController.$inject = ['$uibModalInstance', 'CaloriesService', '$stateParams'];

    function UserCaloriesDeleteController($uibModalInstance, CaloriesService, $stateParams) {
        var vm = this;

        vm.mealInformationId = $stateParams.idMealInfo;

        vm.clear = clear;
        vm.confirmDelete = confirmDelete;

        function clear() {
            $uibModalInstance.dismiss('cancel');
        }

        function confirmDelete(mealInformationId) {
            CaloriesService.delete({idMealInformation: mealInformationId},
                    function () {
                        $uibModalInstance.close(true);
                    });
        }
    }
})();

