(function () {
    'use strict';

    angular
            .module('caloappApp')
            .factory('CaloriesModalService', CaloriesModalService);

    CaloriesModalService.$inject = ['$uibModal', '$state'];

    function CaloriesModalService($uibModal, $state) {
        var service = {
            open: open
        };

        var modalInstance = null;
        var resetModal = function () {
            modalInstance = null;
            $state.go('user-calories', null, {reload: true});
        };

        var resetModalOnClose = function () {
            modalInstance = null;
        };

        return service;

        function open(aMealInformation) {

            if (modalInstance !== null)
                return;
            modalInstance = $uibModal.open({
                animation: true,
                templateUrl: 'app/calories/modal/calories.modal.html',
                controller: 'CaloriesModalController',
                controllerAs: 'vm',
                backdrop: 'static',
                resolve: {
                    translatePartialLoader: ['$translate', '$translatePartialLoader', function ($translate, $translatePartialLoader) {
                            $translatePartialLoader.addPart('calories');
                            return $translate.refresh();
                        }],
                    mealInformation: function () {
                        return aMealInformation;
                    }
                }
            });

            modalInstance.result.then(
                    resetModal,
                    resetModalOnClose
                    );
        }
    }
})();

